export default {
  props: {
    leftFont: {
      typeof: String,
      default: ""
    },
    tabIndex: {
      typeof: Number,
      default: 0
    }
  },
  methods: {
    goBack() {
      if (this.isback == '-1') {
        this.$router.go(-1);
      } else {
        this.$repPath(this.isback);
      }
    }
  },
  setup() {
    return {};
  }
};